<template>
  <div class="content">
    <div class="top_title"></div>
    <!--  -->
    <div class="yure_content">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="first">
          <!-- <div style="text-align: left;margin: 10px 0;">
            <span></span>
            <el-radio v-model="radio1" label="1">Filed</el-radio>
            <el-radio v-model="radio1" label="2">URL</el-radio>
          </div> -->
          <span style="color: #666666;font-size:14px;"></span>
          <el-cascader
            :options="citylist1"
            ref="cascaderAddr"
            :show-all-levels="false"
            v-model="citylabel"
            @change="handleChange"
          ></el-cascader>
          <p
            style="text-align: left;color: #999999;margin: 23px 0;font-size:14px;"
          >
            Filed 10
             10 
          </p>
          <el-input
            type="textarea"
            :rows="2"
            placeholder="url"
            :autosize="{ minRows: 10, maxRows: 20 }"
            v-model="textarea1"
          ></el-input>
          <div style="margin: 20px 0;">
            <el-button
              @click="getrefresh(0)"
              style="width:96px;-height:40px;background:rgba(41,122,255,1);border-radius:2px;color:#ffffff;"
              ></el-button
            >
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <!-- <div style="text-align: left;margin: 10px 0;">
            <span></span>
            <el-radio v-model="radio2" label="1">Filed</el-radio>
            <el-radio v-model="radio2" label="2">URL</el-radio>
          </div> -->
          <span></span>
          <el-cascader
            :options="citylist1"
            v-model="citylabel1"
            :show-all-levels="false"
          ></el-cascader>
          <p
            style="text-align: left;color: #999999;margin: 23px 0;font-size:14px;"
          >
            Filed 10
             10 
          </p>
          <el-input
            type="textarea"
            :rows="2"
            placeholder="url"
            :autosize="{ minRows: 10, maxRows: 20 }"
            v-model="textarea2"
          ></el-input>
          <div style="margin: 20px 0;">
            <el-button
              @click="getrefresh(1)"
              style="width:96px;-height:40px;background:rgba(41,122,255,1);border-radius:2px;color:#ffffff;"
              ></el-button
            >
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="third">
          <!--  -->
          <div class="seach">
            <div class="seach_top">
              <el-input
                placeholder="URL"
                v-model="input"
                class="input-with-select"
                @keyup.enter.native="seachuser"
              >
                <i
                  slot="suffix"
                  class="el-input__icon el-icon-search"
                  @click="seachuser()"
                  style="font-size: 23px;line-height: 60px;font-weight: 600;"
                ></i>
              </el-input>
              <div
                class="seach_top_right"
                @click="option_display()"
                :class="[rotate ? 'sethui' : 'setwhile']"
              >
                
                <i
                  class="el-icon-caret-bottom"
                  :class="[
                    rotate ? 'fa fa-arrow-down go' : 'fa fa-arrow-down aa'
                  ]"
                ></i>
              </div>
            </div>
            <div v-if="optiondisplay" class="seach_bottom">
              <span style="width: 88px;"></span>
              <el-select
                v-model="valuea"
                placeholder=""
                style="width: 160px;"
                @change="seachdata()"
              >
                <el-option label="" value=""></el-option>
                <el-option
                  v-for="(item, index) in options1"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
              <span style="width: 50px;"></span>
              <el-select
                v-model="valueb"
                placeholder=""
                style="width: 160px;"
                @change="seachdata()"
              >
                <el-option label="" value="-1"></el-option>
                <el-option
                  v-for="(item, index) in options2"
                  :key="index"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
              <span style="width: 50px;"></span>
              <el-date-picker
                v-model="value1"
                type="datetimerange"
                range-separator=""
                start-placeholder=""
                end-placeholder=""
                style="width:20%;"
                :picker-options="pickerOptions0"
              ></el-date-picker>
              <el-button
                @click="seachuser()"
                style="margin:0 10px 0 9px;width:91px;height:36px;background:rgba(41,122,255,1);border-radius:4px;color:#ffffff;border:none"
                ></el-button
              >
              <el-button
                style="width:90px;height:36px;background:rgba(219,233,255,1);border-radius:4px;color:#297aff;border:none"
                @click="reset()"
                ></el-button
              >
            </div>
          </div>
          <!--  -->
          <el-table
            :data="tableData"
            stripe
            style="width: 100%"
            :cell-style="rowClass"
            :header-cell-style="headClass"
          >
            <el-table-column prop="url" label=""></el-table-column>

            <el-table-column prop="are" label=""></el-table-column>
            <el-table-column prop="operation_date" label=""
              ><template slot-scope="scope">{{
                scope.row.opt_time | settimes
              }}</template></el-table-column
            >
            <el-table-column prop="operation_type" label=""
              ><template slot-scope="scope">
                <span v-if="scope.row.refresh_type == 1"></span
                ><span v-else></span></template
              ></el-table-column
            >
            <el-table-column prop="operation_status" label=""
              ><template slot-scope="scope">
                <span v-if="scope.row.state == 0"></span>
                <span v-else-if="scope.row.state == 1"></span>
                <span v-else-if="scope.row.state == 2"></span>
                <span v-else-if="scope.row.state == 3"></span>
              </template>
            </el-table-column>
            <el-table-column prop="schedule" label="">
              <template slot-scope="scope">
                <div v-if="scope.row.state == 0" style="color:#297AFF">
                   
                </div>
                <el-progress
                  v-else
                  :percentage="scope.row.progress"
                ></el-progress>
              </template>
            </el-table-column>
          </el-table>
          <div>
            <fenye
              style="text-align:right;margin:37px 0 0 0;"
              @fatherMethod="getpage"
              @fathernum="gettol"
              :pagesa="total_cnt"
              :currentPage="currentPage"
            ></fenye>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    <!--  -->
  </div>
</template>

<script>
import fenye from "@/components/fenye";
import { dateToMs, getymdtime } from "../../servers/sevdate";
import {
  resource_refresh,
  refresh_state,
  getterminal
} from "../../servers/api";
export default {
  data() {
    return {
      currentPage:1,
      errarr: "",
      total_cnt: 1,
      activeName: "first",
      radio1: "1",
      textarea1: "",
      radio2: "1",
      textarea2: "",
      optiondisplay: false,
      input: "",
      valuea: "",
      valueb: "",
      rotate: false,
      value1: "",
      citylabel: "",
      citylabel1: "",
      chanid: "",
      options1: [
        {
          value: 0,
          label: "URL"
        },
        {
          value: 1,
          label: ""
        }
      ],
      options2: [
        {
          value: 0,
          label: ""
        },
        {
          value: 1,
          label: ""
        },
        {
          value: 2,
          label: ""
        }
      ],
      tableData: [],
      citylist1: [
        {
          value: 0,
          label: ""
        },
        {
          value: 1,
          label: "",
          children: [
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            }
          ]
        },
        {
          value: 2,
          label: "",
          children: [
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "qinghai",
              label: ""
            },
            {
              value: "",
              label: ""
            }
          ]
        },
        {
          value: 3,
          label: "",
          children: [
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            }
          ]
        },
        {
          value: 4,
          label: "",
          children: [
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            }
          ]
        },
        {
          value: 5,
          label: "",
          children: [
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            }
          ]
        },
        {
          value: 6,
          label: "",
          children: [
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            }
          ]
        },
        {
          value: 7,
          label: "",
          children: [
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            }
          ]
        },
        {
          value: 8,
          label: "",
          children: [
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "taiwan",
              label: ""
            }
          ]
        }
      ],
      pickerOptions0: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date(
                new Date(new Date().toLocaleDateString()).getTime()
              );
              const start =
                new Date(new Date(new Date().toLocaleDateString()).getTime()) -
                3600 * 1000 * 24 * 1;
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date(
                new Date(new Date().toLocaleDateString()).getTime()
              );
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date(
                new Date(new Date().toLocaleDateString()).getTime()
              );
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 6);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date(
                new Date(new Date().toLocaleDateString()).getTime()
              );
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 29);
              picker.$emit("pick", [start, end]);
            }
          }
        ],
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      },
      errarrs:"",
      errarrs2: ""
    };
  },
  components: {
    fenye
  },
  filters: {
    settimes(data) {
      if (data) {
        var stat = getymdtime(data);
        return stat;
      } else {
        return data;
      }
    }
  },
  created() {},
  mounted() {
    if (this.$cookies.get("id")) {
      this.chanid = this.$cookies.get("id") * 1;
    } else {
      this.$router.push({ path: "/" });
    }
    this.gettoken();
  },
  methods: {
    //token
    gettoken() {
      this.tableData = [];
      let parmas = new Object();
      parmas.chanid = this.chanid;
      parmas.pagesize = 10;
      parmas.page = 0;
      getterminal(parmas)
        .then(res => {
          if (res.status == 0) {
            if (res.result.cols.length <= 0) {
              this.$alert(
                "",
                "",
                {
                  confirmButtonText: "",
                  showClose: false,
                  center: true,
                  callback: action => {
                    this.$router.push({ path: "/terminal_management" });
                  }
                }
              );
            }
          }
        })
        .catch(err => {});
    },
    handleChange() {
      var thsAreaCode = this.$refs.cascaderAddr.getCheckedNodes()[0].pathLabels;
    },
    removeEmpty(arr) {
      for (var i = 0; i < arr.length; i++) {
        if (arr[i] == "" || typeof arr[i] == "undefined") {
          arr.splice(i, 1);
          i = i - 1; // i - 1 , 2 
        }
      }
      return arr;
    },
    getBLen(str) {
      if (str == null) return 0;
      if (typeof str != "string") {
        str += "";
      }
      return str.replace(/[^\x00-\xff]/g, "01").length;
    },
    //---
    getrefresh(datas) {
      let parmas = new Object();
      parmas.buser_id = this.chanid + "";
      if (datas == 0) {
        parmas.type = 0;

        if (this.textarea1 == "") {
          this.$message.error("url");
          return false;
        } else {
          if (this.removeEmpty(this.textarea1.split("\n")).length > 10) {
            this.$message.error("url10");
            return false;
          } else {
            let urlyure = this.removeEmpty(this.textarea1.split("\n"));
            
            let err_num = 0;
            var resyzm = /^http(s)?:\/\/[^\u4e00-\u9fa5]{1,1020}$/;
            this.errarrs = "";
            urlyure.forEach((item, index) => {
              if (resyzm.test(item) === false) {
                this.errarrs = this.errarrs.concat(item + "</br>");
                err_num++;
              } else if (this.getBLen(item) > 1024) {
                this.errarrs = this.errarrs.concat(item + "</br>");
                err_num++;
              }
            });
            if (err_num != 0) {
              this.$alert(`${this.errarrs}url`, "", {
                dangerouslyUseHTMLString: true
              });
              return false;
            } else {
               parmas.url = urlyure;
            }
          }
        }
        if (this.citylabel == "" || this.citylabel.length < 2) {
          this.$message.error("");
          return false;
        } else {
          parmas.area = this.citylabel[1];
        }
      } else {
        parmas.type = 1;
        if (this.textarea2 == "") {
          this.$message.error("url");
          return false;
        } else {
          let urllist = this.removeEmpty(this.textarea2.split("\n"));
          if (urllist.length > 10) {
            this.$message.error("url10");
            return false;
          } else {
            let err_num = 0;
            var resyzm = /^http(s)?:\/\/[^\u4e00-\u9fa5]{1,1020}$/;
            this.errarrs2 = "";
            urllist.forEach((item, index) => {
              if (resyzm.test(item) === false) {
                this.errarrs2 = this.errarrs2.concat(item + "</br>");
                err_num++;
              } else if (this.getBLen(item) > 1024) {
                this.errarrs2 = this.errarrs2.concat(item + "</br>");
                err_num++;
              }
            });
            if (err_num != 0) {
              this.$alert(`${this.errarrs2}url`, "", {
                dangerouslyUseHTMLString: true
              });
              return false;
            } else {
              parmas.url = urllist;
            }
          }
        }
        if (this.citylabel1 == "" || this.citylabel1.length < 2) {
          this.$message.error("");
          return false;
        } else {
          parmas.area = this.citylabel1[1];
        }
      }
      resource_refresh(parmas)
        .then(res => {
          if (res.status == 0) {
            if (res.data.failed_count == 0) {
              this.$message({
                type: "success",
                message: "!"
              });
              if (res.err_code == 165156446464) {
                //
                if (parmas.type == 0) {
                  this.$confirm(
                    "URLURLURL",
                    "",
                    {
                      confirmButtonText: "",
                      cancelButtonText: "",
                      type: "warning"
                    }
                  )
                    .then(() => {
                      this.activeName = "second";
                    })
                    .catch(() => {
                      // this.$message({
                      //   type: "info",
                      //   message: ""
                      // });
                    });
                } else {
                  this.$confirm(
                    "URLURLURL",
                    "",
                    {
                      confirmButtonText: "",
                      cancelButtonText: "",
                      type: "warning"
                    }
                  )
                    .then(() => {
                      this.$message({
                        type: "success",
                        message: "URL!"
                      });
                    })
                    .catch(() => {
                      // this.$message({
                      //   type: "info",
                      //   message: ""
                      // });
                    });
                }
              }
            } else {
              this.errarr = "";
              res.data.res_data.forEach((item, index) => {
                this.errarr = this.errarr.concat(item[0] + "</br>");
              });
              this.$alert(
                `${this.errarr}`,
                res.data.failed_count + "",
                {
                  dangerouslyUseHTMLString: true
                }
              );
            }
          } else {
            this.error.$message("" + res.msg);
          }
        })
        .catch(err => {});
    },
    seachdata() {
      this.getrefreshstate();
    },
    //--
    getrefreshstate() {
      this.tableData = [];
      let parmas = new Object();
      parmas.url = this.input;
      parmas.buser_id = this.chanid + "";
      parmas.refresh_type = this.valuea;
      parmas.state = parseInt(this.valueb);
      if (this.value1 == "") {
        parmas.start_time = "";
        parmas.end_time = "";
      } else {
        parmas.start_time = dateToMs(this.value1[0]);
        parmas.end_time = dateToMs(this.value1[1]);
      }
      refresh_state(parmas)
        .then(res => {
          if (res.status == 0) {
            if (res.data.result.length > 0) {
              this.tableData = res.data.result;
            } else {
              this.$message({
                message: "",
                type: "warning"
              });
            }
          } else {
            this.$message.error(res.err_msg);
          }
        })
        .catch(err => {});
    },
    handleClick(tab, event) {
      this.textarea1 = "";
      this.textarea2 = "";
      if (tab.name == "third") {
        this.getrefreshstate();
      }
    },
    getpage() {},
    gettol() {},
    //
    option_display() {
      this.optiondisplay = !this.optiondisplay;
      this.rotate = !this.rotate;
    },
    //
    seachuser() {
      // this.optiondisplay = false;
      this.getrefreshstate();
    },
    //
    reset() {
      this.input = "";
      this.valuea = "";
      this.valueb = "";
      this.value1 = "";
      this.getrefreshstate();
    },
    // 
    headClass() {
      return "text-align: center;background:#F3F6FB;color:#333333;font-size:16px;";
    },
    // 
    rowClass() {
      return "text-align: center;";
    }
  }
};
</script>

<style lang="scss" scoped>
.content {
  .yure_content {
    width: 100%;
    // margin: 15px 0;
  }
  width: 100%;
  height: 100%;
  text-align: left;
  .seach {
    width: 100%;
    // display: flex;
    // align-items: center;
    .seach_top {
      width: 100%;
      height: 60px;
      line-height: 60px;
      .input-with-select {
        width: 300px;
        float: left;
        margin-right: 10px;
      }
    }
    .seach_bottom {
      text-align: left;
      height: 80px;
      background: #f2f6fa;
      border-radius: 10px;
      padding: 10px 15px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin-bottom: 20px;
      span {
        color: #999999;
        font-size: 14px;
        margin-left: 10px;
      }
    }
  }
}
//
.aa {
  transition: all 1s;
}
.go {
  transform: rotate(-180deg);
  transition: all 1s;
}
</style>



