import router from '../router/index'
import http from "./ajax"
import VueCookies from 'vue-cookies'
import axios from 'axios';
import { Message } from 'element-ui';
// import store from '../store/index'
// 
// let rootsd = process.env.NODE_ENV;
// switch (rootsd) {
//     case "development": //
//         axios.defaults.baseURL = 'http://xyj.grapefruitcloud.com';
//         break;
//     case "production": //
//         axios.defaults.baseURL = 'http://xyj.grapefruitcloud.com';
//         break;
//     default: //
//         axios.defaults.baseURL = 'http://39.100.131.247';
// }
var href = window.location.href

if (href.indexOf('xyj.grapefruitcloud.com') >= 0) {
    var userUrl = 'http://xyj.grapefruitcloud.com' //PHPURL 
} else {
    var userUrl = 'http://39.100.131.247'
    var yourUrl = 'http://test.zzb.com'
        // var yourUrl = 'http://10.0.0.140:8090'
}
// 
axios.defaults.timeout = 10000;
// post
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

// 
axios.interceptors.request.use(
        config => {
            var checkwithout = [
                userUrl + "/clouduser/loginbyphone",
                userUrl + "/clouduser/login",
                userUrl + "/clouduser/register",
                userUrl + "/clouduser/getcode",
                userUrl + "/clouduser/getemail",
                userUrl + "/clouduser/checktoken",
                userUrl + "/clouduser/forgetpassword",
                userUrl + "/clouduser/resetpassword"
            ];
            if (checkwithout.indexOf(config.url) < 0) {
                http.request({
                    "url": userUrl + "/clouduser/checktoken",
                    "method": "post",
                    "data": { "id": VueCookies.get("id"), "token": VueCookies.get("token") },
                    async: true,
                    success: function(data) {
                        data = JSON.parse(data)
                        if (data.status != 0) {
                            VueCookies.set("id", "", 0);
                            VueCookies.set("token", "", 0);
                            VueCookies.set("user", "", 0);
                            alert("")
                            router.push("/")
                        }
                    }
                })
            }
            return config;
            // tokenhttpheadertoken
            // tokentoken
            // const token = store.state.token;
            // token && (config.headers.Authorization = token);

        },
        error => {
            return Promise.error(error);
        })
    // 
axios.interceptors.response.use(
    response => {

        //console.log(response.status)
        if (response.status === 200) {
            //console.log(Promise.resolve(response));
            if (response.data.status == -5) {
                Message.error('!');
            }
            return Promise.resolve(response);
        } else {
            return Promise.reject(response);
        }
    },
    // 200
    error => {
        //console.log(error.response.status);
        if (error.response.status) {
            switch (error.response.status) {
                // 401: 
                // 
                // 
                case 400:
                    Message.error('!');
                    break;
                    // 403 token
                    // 
                    // tokenvuextoken
                    // 
                case 404:
                    Message.error('!');
                    break;
                    // 404
                case 500:
                    Message.error('!');
                    break;
                    // 
                default:
                    this.$message(
                        error.response.data.message

                    );
            }
            return Promise.reject(error.response);
        }
    }
);
/**
 * getget
 * @param {String} url [url]
 * @param {Object} params []
 */
export function get(url, params) {
    return new Promise((resolve, reject) => {
        axios.get(url, {
                params: params
            })
            .then(res => {
                resolve(res.data);
            })
            .catch(err => {
                // console.log(err.data);
                reject(err.data)
            })
    });
}
/**
 * postpost
 * @param {String} url [url]
 * @param {Object} params []
 */
export function post(url, params) {
    return new Promise((resolve, reject) => {
        axios.post(url, params)
            .then(res => {
                resolve(res.data);
            })
            .catch(err => {
                reject(err.data)
            })
    });
}


